// appointment APIs

import { Break } from "@/apis/Break.ts";
import { Client } from "@/apis/Client.ts";
import { Employee } from "@/apis/Employee.ts";
import { Service } from "@/apis/Service.ts";

export class Appointment {
    id: number;                                     // appointment ID
    timeslot: number;                               // initial timeslot of appointment
    slots: number;                                  // # of timeslots for appointment
    type: string;                                   // type of appointment (i.e. service or break)
    label: string;
    employee: number | Employee;                    // employee's info
    isRequested: boolean;                           // is employee requested
    client: number | Client;                        // selected client's info
    services: Array<number> | Array<Service>;       // selected service's info
    apptBreak: number | Break;                      // break's info (if type: break)
    // date: Date;
    modified: string;                               // last modified (date & person)

    constructor ({
        id = -1,
        timeslot = -1,
        slots = -1,
        type = "",
        label = "",
        employee = -1,
        isRequested = false,
        client = -1,
        services = [],
        apptBreak = -1,
        // date = ,
        modified = ""
    }: any = {}) {
        this.id = id;
        this.timeslot = timeslot;
        this.slots = slots;
        this.type = type;
        this.label = label;
        this.employee = employee;
        this.isRequested = isRequested;
        this.client = client;
        this.services = services;
        this.apptBreak = apptBreak;
        // this.date = date;
        this.modified = modified;
    }

    public static GetAppointments(): Promise<Array<Appointment>> {
        return new Promise((resolve, reject) => {
            const data: Array<Appointment> = [
                new Appointment({
                    id: 0,
                    timeslot: 1,
                    slots: 2,
                    type: "service",
                    employee: 0,
                    isRequested: true,
                    client: 0,
                    services: [3],
                    // date: ,
                    modified: `${ new Date() } by EmployeeName`
                }),
                new Appointment({
                    id: 1,
                    timeslot: 0,
                    slots: 4,
                    type: "break",
                    employee: 2,
                    apptBreak: 2,
                    // date: ,
                    modified: `${ new Date() } by EmployeeName`
                }),
                new Appointment({
                    id: 2,
                    timeslot: 1,
                    slots: 3,
                    type: "service",
                    employee: 4,
                    client: 1,
                    services: [0, 6],
                    // date: ,
                    modified: `${ new Date() } by EmployeeName`
                })
            ];
            resolve(data);
        });
    }
}